
import { resultScreenData } from "@/store/questionnaire";
import { defineComponent } from "vue";

export default defineComponent({
  name: "PrintView2",
  props: {
    resultNumber: {
      type: Number,
      default: 2,
    },
    medicine: {
      type: String,
      default: "",
    },
    addition: {
      type: String,
      default: "",
    },
    answers: {
      type: Array,
      default: () => {
        [];
      },
    },
    riskL: {
      type: Array,
      default: () => ["", "", ""],
    },
    riskH: {
      type: Array,
      default: () => ["", ""],
    },
  },
  setup(props) {
    const rslt = resultScreenData.list[props.resultNumber];
    return {
      rslt,
    };
  },
});
